import React from "react";
import styled from "styled-components";
import Typewriter from "typewriter-effect";
import Button from "./Button";
import Twitter from "./icons/Twitter";
import Instagram from "./icons/Instagram";
const Title = styled.h2`
  font-size: ${(props) => props.theme.fontxxl};
  text-transform: capitalize;
  width: 80%;
  color: ${(props) => props.theme.text};
  // align-self: flex-start;
  text-align: center;
  font-weights: 900;

  span {
    font-family: "noto-serif-jp", cursive;
    font-size: ${(props) => props.theme.fontsm};
  }
  .text-1 {
    color: blue;
  }
  .text-2 {
    color: orange;
  }
  .text-3 {
    color: red;
  }

  @media (max-width: 70em) {
    font-size: ${(props) => props.theme.fontxl};
  }
  @media (max-width: 48em) {
    align-self: center;
    text-align: center;
  }
  @media (max-width: 40em) {
    width: 90%;
  }
`;
const SubTitle = styled.h3`
  font-size: ${(props) => props.theme.fontlg};
  text-transform: capitalize;
  color: ${(props) => `rgba(${props.theme.textRgba}, 0.6)`};
  font-weight: 600;
  margin-bottom: 1rem;
  margin-top: 1.5rem;

  width: 80%;
  // align-self: flex-start;
  text-align: center;

  @media (max-width: 40em) {
    font-size: ${(props) => props.theme.fontmd};
  }

  @media (max-width: 48em) {
    align-self: center;
    text-align: center;
  }
`;

const SocialContainer = styled.div`
  width: 80%;
  // align-self: flex-start;
  text-align: center;

  @media (max-width: 48em) {
    align-self: center;
    text-align: center;

    button {
      margin: 0 auto;
    }
  }
`;
const TypeWriterText = () => {
  return (
    <>
      <Title>
        Embrace Your Roots
        <Typewriter
          options={{
            autoStart: true,
            loop: true,
          }}
          onInit={(typewriter) => {
            typewriter
              .typeString(
                `<span class="text-1">Discover The Mystery Behind</span>`
              )
              .pauseFor(200)
              .deleteAll()
              .typeString(`<span class="text-2">The Pond.</span>`)
              .pauseFor(200)
              .deleteAll()
              .start();
          }}
        />
      </Title>
      <SubTitle>Coming Winter 2022</SubTitle>
      <SocialContainer>
        <a
          href="https://twitter.com/renkonchi"
          target="_blank"
          rel="noopener noreferrer"
          aria-label="twitter"
        >
          <Twitter />
        </a>
        <a
          href="https://www.instagram.com/renkonchi/"
          target="_blank"
          rel="noopener noreferrer"
          aria-label="instagram"
        >
          <Instagram />
        </a>
      </SocialContainer>
    </>
  );
};

export default TypeWriterText;
